.home-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #1a1a1a;
  color: #d4af37;
}

.main-content {
  flex: 1;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.feature-card {
  transition: transform 0.2s;
  background-color: #1e3a2b !important;
  border: 1px solid #d4af37;
  color: #d4af37 !important;
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(212, 175, 55, 0.2);
  background-color: #1e3a2b !important;
  color: #d4af37 !important;
}

.news-card {
  background-color: #1e3a2b !important;
  border: 1px solid #d4af37;
  color: #d4af37 !important;
  height: 100%;
  max-height: 800px;
  overflow-y: auto;
  transition: all 0.3s ease;
}

.news-card:hover {
  box-shadow: 0 4px 12px rgba(212, 175, 55, 0.3);
}

.news-card::-webkit-scrollbar {
  width: 8px;
}

.news-card::-webkit-scrollbar-track {
  background: #1a1a1a;
  border-radius: 4px;
}

.news-card::-webkit-scrollbar-thumb {
  background: #d4af37;
  border-radius: 4px;
}

.news-card::-webkit-scrollbar-thumb:hover {
  background: #b8860b;
}

.news-card .MuiCardContent-root {
  padding: 20px;
}

.news-card .MuiDivider-root {
  background-color: rgba(212, 175, 55, 0.2);
  margin: 16px 0;
}

.news-card .MuiTypography-h6 {
  font-size: 1.1rem;
  line-height: 1.4;
  margin-bottom: 8px;
  font-weight: 600;
}

.news-card .MuiTypography-body2 {
  font-size: 0.9rem;
  line-height: 1.5;
  color: rgba(212, 175, 55, 0.8) !important;
}

.news-card .MuiTypography-caption {
  font-size: 0.8rem;
  color: rgba(212, 175, 55, 0.6) !important;
}

.news-card .news-source {
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.75rem;
}

.features-flex-wrapper {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.feature-flex-item {
  width: 100%;
}

@media (min-width: 900px) {
  .features-flex-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 24px;
  }
  .feature-flex-item {
    flex: 1 1 calc(33.333% - 16px);
    min-width: 0;
    max-width: calc(33.333% - 16px);
  }
  .feature-flex-item-full {
    flex-basis: 100%;
    max-width: 100%;
    margin-top: 24px;
  }
  .feature-flex-item-half {
    flex: 1 1 calc(50% - 12px);
    max-width: calc(50% - 12px);
    min-width: 0;
  }
}