.account-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #1a1a1a 0%, #2c2c2c 100%);
}

.account-container .MuiPaper-root {
  transition: transform 0.3s ease;
}

.account-container .MuiPaper-root:hover {
  transform: translateY(-5px);
}

.account-container .MuiAvatar-root {
  transition: transform 0.3s ease;
}

.account-container .MuiAvatar-root:hover {
  transform: scale(1.1);
} 