.match-discussion-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #1a1a1a 0%, #2a2a2a 100%);
}

.match-discussion-container .MuiPaper-root {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.match-discussion-container .MuiPaper-root:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(255, 215, 0, 0.2);
}

.match-discussion-container .MuiCard-root {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.match-discussion-container .MuiCard-root:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(255, 215, 0, 0.2);
}

.match-discussion-container .MuiAvatar-root {
  transition: transform 0.3s ease;
}

.match-discussion-container .MuiAvatar-root:hover {
  transform: scale(1.1);
}

.match-discussion-container .MuiTextField-root {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.match-discussion-container .MuiTextField-root .MuiInputBase-input {
  color: #ffffff;
}

.match-discussion-container .MuiTextField-root .MuiInputLabel-root {
  color: rgba(255, 215, 0, 0.7);
}

.match-discussion-container .MuiTextField-root .MuiOutlinedInput-notchedOutline {
  border-color: rgba(255, 215, 0, 0.2);
}

.match-discussion-container .MuiTextField-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: rgba(255, 215, 0, 0.5);
}

.match-discussion-container .MuiTextField-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: #ffd700;
}

.match-discussion-container .MuiButton-root {
  text-transform: none;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.match-discussion-container .MuiListItem-root {
  transition: background-color 0.3s ease;
}

.match-discussion-container .MuiListItem-root:hover {
  background-color: rgba(255, 215, 0, 0.05);
} 